<template>
  <div class="row">
    <div v-show="isActive === 'video'" class="col-12">
      <lesson-video v-if="lessonData.video"
                    :video="lessonData.video"
                    :title="lessonData.title"/>
    </div>
    <div v-show="isActive !== 'video'" class="col-12">
      <div class="card mb-0">
        <img
          class="card-img"
          v-lazy="lessonImg"
          :alt="lessonData.title"
        >
        <div class="card-img-overlay d-flex align-items-center" @click.prevent="player.togglePlay">
          <div class="mx-auto">
            <span><i class="fas fa-backward text-white jump mr-3 emphasize-dark" @click.prevent="jumpBack"></i></span>
            <span><i class="far text-white play-button emphasize-dark" :class="[ isPlaying ? 'fa-pause-circle' : 'fa-play-circle' ]"></i></span>
            <span><i class="fas fa-forward text-white jump ml-3 emphasize-dark" @click.prevent="jumpAhead"></i></span>
          </div>
        </div>
      </div>
      <vue-plyr ref="plyr" :options="playerOptions">
        <audio controls>
          <source :src="audioSource" type="audio/mp3"/>
        </audio>
      </vue-plyr>
    </div>
    <div class="col-12 mt-2">
      <div class="row">
        <div class="col-12">
          <div class="row">
            <div class="col-auto">
              <base-switch :value="autoMarkStudied" @input="toggleAutoMarkStudied($event)"></base-switch>
            </div>
            <div class="col small pt-1">Automatically mark lessons as completed
              <el-popover placement="top" trigger="hover" width="240" title="Automatically Complete">
                <i class="fa fa-info-circle" slot="reference"></i>
                <span class="text-left">We'll mark lessons as completed once you cross the 95% mark. Don't worry, they'll always be available in your <router-link class="btn-link" to="/history">History</router-link> area.</span>
              </el-popover>
            </div>
          </div>
        </div>
        <div v-if="lessonData.video" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setVideo" :type="isActive === 'video' ? 'primary' : 'secondary'" class="btn-block"><i class="fas fa-film"></i> Video Lesson</base-button>
        </div>
        <div v-if="lessonData.mp3_private" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setAudio('lesson')" :type="isActive === 'lesson' ? 'primary' : 'secondary'" class="btn-block"><i class="fas fa-podcast"></i> Audio Lesson</base-button>
        </div>
        <div  v-if="lessonData.mp3_dialogue" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setAudio('dialogue')" :type="isActive === 'dialogue' ? 'primary' : 'secondary'" class="btn-block"><i class="ni icon-speaking"></i> Dialogue</base-button>
        </div>
        <div v-if="lessonData.mp3_thefix" class="col-6 col-md-4 mt-2">
          <base-button @click.prevent="setAudio('review')" :type="isActive === 'review' ? 'primary' : 'secondary'" class="btn-block"><i class="ni icon-vocabulary"></i> Review</base-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import qingWen from '@/assets/img/qing-wen.jpg'
  import {mapState, mapMutations} from 'vuex'

  import LessonVideo from "./Video";

  export default {
    name: "LessonMedia",
    components: {LessonVideo},
    title () { return `${this.lessonData.title} | ChinesePod`},
    computed: {
      audioSource () {
        return process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_private']
      },
      player () { return this.$refs.plyr.player },
      lessonImg () {
        let imgSlug = this.lessonData.image;
        if (imgSlug.slice(0,4) === 'http') {
          return imgSlug
        } else {
          return this.lessonData.id[0] === 'Q'
            ? `${this.qingWen}`
            : `https://s3contents.chinesepod.com${this.lessonData.type === 'extra' ? '/extra/' : '/'}${this.lessonData.id}/${this.lessonData.hash_code}/${this.lessonData.image}`
        }
      },
      ...mapState({
        autoMarkStudied: state => state.userInfo.autoMarkStudied
      })
    },
    props: {
      lessonData: Object
    },
    methods: {
      async setAudio (input) {
        if (this.loading) {
          return
        }
        this.loading = true;
        switch (input) {
          default:
            this.isActive = input;
            this.player.source = {
              type: 'audio',
              title: 'Audio Lesson',
              sources: [
                {
                  src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_private'],
                  type: 'audio/mp3'
                }
              ]
            };
            await this.player.play();
            this.$gtag.event('Play', {
              'event_category': 'Audio',
              'event_label': this.lessonData.title
            });
            break;

          case 'dialogue':
            this.isActive = input;
            this.player.source = {
              type: 'audio',
              title: 'Audio Dialogue',
              sources: [
                {
                  src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_dialogue'],
                  type: 'audio/mp3'
                }
              ]
            };
            await this.player.play();
            break;

          case 'review':
            this.isActive = input;
            this.player.source = {
              type: 'audio',
              title: 'Audio Review',
              sources: [
                {
                  src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_thefix'],
                  type: 'audio/mp3'
                }
              ]
            };
            await this.player.play();
            break;
        }

        this.$gtag.event('select_content', {
          'event_category': 'engagement',
          'event_label': input
        });

        this.loading = false;

      },
      setVideo () {
        this.player.pause();
        this.isActive = 'video';

        this.$gtag.event('select_content', {
          'event_category': 'engagement',
          'event_label': 'video'
        });

      },
      jumpAhead () {
        this.player.forward();
        this.player.togglePlay()
      },
      jumpBack () {
        this.player.rewind();
        this.player.togglePlay()
      },
      async logProgress (current) {

        if (this[`${this.isActive}Progress`] < current) {

          if (!this.sendingProgress) {
            this.sendingProgress = true;
            this[`${this.isActive}Progress`] = current;
            await this.$http.post(process.env.VUE_APP_ROOT_API + '/api/v1/lessons/progress', {
              lessonId: this.lessonData.id,
              track: this.isActive,
              progress: current
            }).catch(() => {});
            this.$gtag.event(`${current}% Listened - ${this.isActive}`, {
              'event_category': 'Audio',
              'event_label': this.lessonData.title
            });
            this.sendingProgress = false;
          }

          if (this.autoMarkStudied && current > 90 && ['lesson', 'video'].includes(this.isActive)) {
            this.$emit('studied');
          }
        }
      },
      ...mapMutations({
        toggleAutoMarkStudied: 'TOGGLE_AUTO_MARK_STUDIED'
      })
    },
    data () {
      return {
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time', 'mute', 'volume', 'captions', 'settings', 'pip', 'airplay', 'fullscreen', 'controls'],
          settings: ['captions', 'quality', 'speed', 'loop', 'controls'],
          loop: {active: false}
        },
        isActive: 'lesson',
        isPlaying: false,
        qingWen: qingWen,
        loading: false,
        maxProgress: 0,
        videoProgress: -1,
        lessonProgress: -1,
        reviewProgress: -1,
        dialogueProgress: -1,
        historicalProgress: 0,
        sendingProgress: false
      }
    },
    mounted() {
      if (this.lessonData && this.lessonData.video) {
        let wistiaScript = document.createElement('script');
        wistiaScript.setAttribute('src', 'https://fast.wistia.net/assets/external/E-v1.js');
        document.head.appendChild(wistiaScript);
        this.isActive = 'video';
        window._wq = window._wq || [];
        _wq.push({ id: this.lessonData.video, onReady: (video) => {
            video.bind('timechange', async (t) => {
              let current = Math.floor(t / video.duration() * 100 / 5) * 5;
              await this.logProgress(current);
            });
          }});
      }
      this.player.on('playing', () => {
        this.isPlaying = true;

        if ('mediaSession' in navigator) {
          navigator.mediaSession.metadata = new MediaMetadata({
            title: this.lessonData.title,
            artist: 'ChinesePod',
            album: `${this.lessonData.level} Lesson`,
            artwork: [
              { src: 'https://www.chinesepod.com/home/img/icons/icon_64.png',  sizes: '64x64',   type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_64.png',  sizes: '96x96',   type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_120.png', sizes: '120x120', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_120.png', sizes: '128x128', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_192.png', sizes: '192x192', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_192.png', sizes: '256x256', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_384.png', sizes: '384x384', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_512.png', sizes: '512x512', type: 'image/png' }
            ]
          });
          navigator.mediaSession.playbackState = 'playing';
          navigator.mediaSession.setActionHandler('play', () => this.player.togglePlay());
          navigator.mediaSession.setActionHandler('pause', () => this.player.togglePlay());
          navigator.mediaSession.setActionHandler('seekbackward', () => this.player.rewind());
          navigator.mediaSession.setActionHandler('seekforward', () => this.player.forward());
          // navigator.mediaSession.setActionHandler('previoustrack', function() {});
          // navigator.mediaSession.setActionHandler('nexttrack', function() {});
        }
      });
      this.player.on('pause', () => {
        this.isPlaying = false;
        if ('mediaSession' in navigator) {
          navigator.mediaSession.playbackState = "paused";
        }
      });
      this.player.on('timeupdate', async () => {
        let current = Math.floor((this.player.currentTime / this.player.duration) * 100 / 5) * 5;
        await this.logProgress(current);
      });
    }
  }
</script>

<style >
  @import '~plyr/dist/plyr.css';
  .play-button {
    font-size: 100px;
  }
  .jump {
    font-size: 60px;
  }
  .emphasize-dark {
    text-shadow: 0 0 5px black;
  }
</style>
