<template>
  <div>
    <base-header type="success" class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">{{$route.name}}</h6>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block ml-md-4">
            <route-breadcrumb></route-breadcrumb>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt--6">
      <div class="card-wrapper">
        <transition name="fade">
          <div v-if="lessonData && lessonData.length > 0" id="lessons" :class="!lessonData ? 'min-vh-100' : ''">
            <card>
              <div class="row">
                <lesson-card v-for="lesson in lessonData" :key="lesson.id" :lesson="lesson" class="col-6 col-md-3"></lesson-card>
              </div>
            </card>
            <div id="controls" class="text-center">
              <base-pagination
                :page-count="pages"
                :per-page="perPage"
                :value="page"
                align="center"
                @input="listener($event)"
              ></base-pagination>
            </div>
          </div>
        </transition>
      </div>
      <loading :active.sync="isLoading"
               :can-cancel="true"
               :on-cancel="onCancel"
               :is-full-page="fullPage"></loading>
    </div>
  </div>
</template>

<script>
  import LessonCard from "../../components/Dashboard/LessonCard";

  // Import component
  import Loading from 'vue-loading-overlay';
  // Import stylesheet
  import 'vue-loading-overlay/dist/vue-loading.css';

  export default {
    name: "bookmarks",
    components: {LessonCard, Loading},

    data () {
      return {
        pages: 10,
        perPage: 16,
        page: 1,
        lessonData: [],
        isLoading: false,
        fullPage: true
      }
    },
    async mounted() {
      if (this.$route.query && this.$route.query.page) {
        this.page = parseInt(this.$route.query.page);
        this.pages = this.page + 1;
      }
      await this.getLessons();

    },
    async beforeRouteUpdate(to, from, next) {
      if (to.query && to.query.page) {
        this.page = parseInt(to.query.page);
      }
      await this.getLessons();
      next()
    },
    methods: {
      async listener (event) {
        if (!this.isLoading) {

          this.$router.push({ name: 'Bookmarks', query: { page: event } });

        }
      },
      async getLessons () {
        this.isLoading = true;
        this.lessonData = await this.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/get-bookmarked-lessons?limit=${this.perPage}&skip=${(this.page - 1) * this.perPage}`)
          .then(({data}) => {
            this.pages = Math.ceil(data.count / this.perPage);
            return data.lessons
          })
          .catch(() => {window.location.href = '/login'});
        this.isLoading = false;
      }
    }
  }
</script>

