<template>
  <router-link :to="`/lesson/${lesson.slug}${courseId ? '/' + courseId : ''}`">
    <div  class="card bg-transparent shadow-none mb-0">
      <img
        class="card-img"
        :class="lessonLevel()"
        v-lazy="lessonImg"
        :alt="lesson.title"
      >
      <div v-show="!hideButtons" class="card-img-overlay rounded shadow-none" :class="[lesson.studied ? 'bg-translucent-dark' : 'bg-transparent']">
        <div class="float-right">
          <button
            class="btn btn-secondary btn-circle btn-circle-sm mt--4 mr-1"
            :class="[lesson.saved ? 'text-danger' : '']"
            @click.prevent="markSaved(lesson)"
          >
            <i
              :class="[lesson.saved ? 'fas' : 'far', loadingSaved ? 'fas fa-spinner fa-spin' : 'fa-bookmark']"
            ></i>
          </button>
          <button
            class="btn btn-secondary btn-circle btn-circle-sm mt--4 mr--3"
            :class="[lesson.studied ? 'text-danger' : '']"
            @click.prevent="markStudied(lesson)"
          ><i
            :class="[lesson.studied ? 'fas' : 'far', loadingStudied ? 'fas fa-spinner fa-spin' : 'fa-check-circle ni-lg']"
          ></i></button>
        </div>
      </div>
    </div>
    <div class="card-body p-0 pb-4">
      <h2 class="card-title m-0">{{ lesson.title }}</h2>
    </div>
  </router-link>
</template>
<script>
  import qingWen from '@/assets/img/qing-wen.jpg'

  export default {
    name: "lessonCard",
    props: {
      lesson: {
        type: Object,
        description: "Lesson data to show"
      },
      courseId: [String, Number],
      hideButtons: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        qingWen: qingWen,
        loadingSaved: false,
        loadingStudied: false
      }
    },
    computed: {
      lessonImg () {
        let imgSlug = this.lesson.image;
        if (imgSlug.slice(0,4) === 'http') {
          return imgSlug
        } else {
          return this.lesson.id[0] === 'Q'
            ? `${this.qingWen}`
            : `https://s3contents.chinesepod.com${this.lesson.type === 'extra' ? '/extra/' : '/'}${this.lesson.id}/${this.lesson.hash_code}/${this.lesson.image}`
        }
      }
    },
    methods: {
      lessonLevel () {
        return this.lesson.level.split(' ').length > 1
          ? this.lesson.level.split(' ').join('-').toLowerCase()
          : this.lesson.level.toLowerCase()
      },
      markStudied: async function (lesson) {
        if (!this.loadingStudied) {
          this.loadingStudied = true;
          lesson.studied = !lesson.studied;
          this.$forceUpdate();
          await this.$http.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-studied', {
            lessonId: lesson.id,
            status: lesson.studied
          }).catch(() => {});
          this.loadingStudied = false;
        }
      },
      markSaved: async function (lesson) {
        if (!this.loadingSaved) {
          this.loadingSaved = true;
          lesson.saved = !lesson.saved;
          this.$forceUpdate();
          await this.$http.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-saved', {
            lessonId: lesson.id,
            status: lesson.saved
          }).catch(() => {});
          this.loadingSaved = false;
        }
      }
    }
  }
</script>
