<template>
  <div>
    <div>
      <base-button v-if="premium"  class="d-block float-right" size="sm" @click="print"><i class="fas fa-print"></i> Print</base-button>
      <base-button class="float-right" size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉字' : 'Trad: 漢字'}}</base-button>
      <h2 class="h2">Dialogue</h2>
    </div>
    <div class="pt-2">
      <dialogue-item
        v-for="(sentence, index) in dialogueSegments"
        :key="index"
        :sentence-index="index"
        :speaker="sentence.speaker"
        :cn="sentence.sentence"
        :en="sentence.en"
        tab="dialogue"
        :audioSource="audioSource(sentence)"
        class="col-lg-7"
        :class="index & 1 ? 'float-right' : 'float-left'"
      />
    </div>
    <upgrade v-if="!premium && truncateData" hide-bullets class="col-12 m-0 p-0" title="Examples have been shortened. Upgrade to Access All Premium Features."/>
    <div v-if="premium" id="lessonDialogue" class="row d-none">
      <div class="col-3">
        <img src="@/assets/img/brand/brand.svg" class="img-fluid" alt="ChinesePod"/>
      </div>
      <h1 align="center" class="p-3">{{ lessonData.title }} - Dialogue</h1>
      <div class="px-5 py-2">
        <div v-for="segment in dialogueData.dialogue" :key="segment.display_order" class="py-3 col-12">
          <div v-if="segment.speaker">{{segment.speaker}}:</div>
          <div style="font-size: 1.4rem; font-family: 'Noto Sans SC', sans-serif;" v-html="segment[charSet.slice(0,1)]"></div>
          <div v-html="convertPinyin(segment.p)"></div>
          <div v-html="segment.en"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import DialogueItem from "./DialogueItem";
  import Upgrade from "./Upgrade";
  import convertPinyinTones from 'pinyin-tone-converter';

  export default {
    name: "Dialogue",
    components: {Upgrade, DialogueItem},
    props: {
      lessonData: Object,
      dialogueData: Object
    },
    computed: {
      lessonId () {
        return this.lessonData.id
      },
      hash_code () {
        return this.lessonData.hash_code
      },
      extra () {
        return this.lessonData.extra
      },
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      audioRoot() {
        return process.env.VUE_APP_ROOT_S3 + (this.extra ? '/extra/' : '/') +`${this.lessonId}/${this.hash_code}`
      },
      dialogueSegments () {
        return !this.truncateData ? this.dialogueData.dialogue : this.dialogueData.dialogue.slice(0,2)
      },
      upgradePath () {
        return this.$store.state.userInfo.upgrade.upgradePath
      },
      truncateData () {
        return !this.premium && this.upgradePath === 2
      }
    },
    methods: {
      async toggleCharSet () {
        await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
      },
      audioSource (sentence) {
        return sentence.audio.slice(0,4) === 'http'
          ? sentence.audio
          : process.env.VUE_APP_ROOT_S3 + (this.extra ? '/extra/' : '/') +`${this.lessonId}/${this.hash_code}` + '/' + sentence.audio
      },
      print () {
        this.wantsPrint = true;
        this.$htmlToPaper('lessonDialogue');
      },
      convertPinyin (pinyin) {
        return convertPinyinTones(pinyin);
      }
    }
  }
</script>

