<template>
  <div>
    <div>
      <base-button v-if="premium"  class="d-block float-right" size="sm" @click="print"><i class="fas fa-print"></i> Print</base-button>
      <base-button class="float-right" size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉字' : 'Trad: 漢字'}}</base-button>
      <h2 class="h2">Expansion</h2>
    </div>
    <div class="pt-2">
      <div v-for="expansion in expansionSegments" :key="expansion.phrase" class="offset-lg-2 col-lg-8 pb-4">
        <h3 class="h3 example ml-2">{{ expansion.phrase }}</h3>
        <dialogue-item
          v-for="(sentence, index) in expansion.examples"
          :key="index"
          :sentence-index="index"
          :cn="sentence.sentence"
          :en="sentence.en"
          :tab="`expansion-${sentence.id}`"
          :audioSource="`${audioRoot}/${sentence.audio}`"
          class="" />
      </div>
    </div>
    <upgrade v-if="!premium && truncateData" hide-bullets class="col-12 m-0 p-0" title="Examples have been shortened. Upgrade to Access All Premium Features."/>
    <div v-if="premium" id="lessonExpansion" class="row d-none">
      <div class="col-3">
        <img src="@/assets/img/brand/brand.svg" class="img-fluid" alt="ChinesePod"/>
      </div>
      <h1 align="center" class="p-3">{{ lessonData.title }} - Expansion</h1>
      <div class="px-5 py-2">
        <div v-for="(teachingPoint, index) in expansionData" :key="index" class="py-3 col-12">
          <h2 v-html="teachingPoint.phrase"></h2>
          <div v-for="example in teachingPoint.examples" :key="example.id" class="pb-3">
            <div style="font-size: 1.4rem; font-family: 'Noto Sans SC', sans-serif;" v-html="example[charSet.slice(0,1)]"></div>
            <div v-html="convertPinyin(example.p)"></div>
            <div v-html="example.en"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import DialogueItem from "./DialogueItem";
  import Upgrade from "./Upgrade";

  import { mapState, mapMutations } from 'vuex'
  import {
    TOGGLE_PINYIN
  } from '@/mutation-types'

  import convertPinyinTones from 'pinyin-tone-converter';

  export default {
    name: "Expansion",
    components: {Upgrade, DialogueItem},
    props: {
      lessonData: Object,
      expansionData: [Object, Array]
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      audioRoot() {
        return process.env.VUE_APP_ROOT_S3 + (this.lessonData.extra ? '/extra/' : '/') +`${this.lessonData.id}/${this.lessonData.hash_code}`
      },
      expansionSegments () {
        return !this.truncateData ? this.expansionData : this.expansionData.slice(0,1)
      },
      truncateData () {
        return !this.premium && this.upgradePath === 2
      },
      ...mapState({
        upgradePath: state => state.userInfo.upgrade.upgradePath,
        pinyin: state => state.userInfo.pinyin,
        charSet: state => state.userInfo.charSet
      })
    },
    methods: {
      async toggleCharSet () {
        await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
      },
      print () {
        this.wantsPrint = true;
        this.$htmlToPaper('lessonExpansion');
      },
      convertPinyin (pinyin) {
        return convertPinyinTones(pinyin);
      },
      ...mapMutations({
        togglePinyin: TOGGLE_PINYIN
      })
    }
  }
</script>

<style scoped>
  .example {
    font-size: 40px;
  }
</style>
