<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"><i class="fas fa-home"></i> - Dashboard</router-link>
          <h1 class="h2 d-inline-block mb-0 mr-md-4">Explore Lessons</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item"><router-link to="/"><i class="fas fa-home"></i></router-link></li>
              <li class="breadcrumb-item active" aria-current="page"><router-link to="/explore">Explore</router-link></li>
            </ol>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-md--6">
      <div id="searchArea" class="col-12 col-lg-8 offset-lg-2">
        <base-input group class="mb-2" prepend-icon="fas fa-search">
          <input type="search" class="form-control" placeholder="Search lessons, courses, topics or hosts" aria-label="Search lessons, courses, topics or hosts" aria-describedby="button-addon2" v-model.trim="searchTerm" @search="runSearch()">
          <div class="input-group-append">
            <base-button class="btn btn-outline-primary is-loading" type="button" id="button-addon2" @click="runSearch()" :loading="searching">Search</base-button>
          </div>
        </base-input>
        <div class="col-12">
          <div class="row">
            <base-button size="sm" outline class="ml-auto" @click="showAdvancedOptions = !showAdvancedOptions">{{ showAdvancedOptions ? 'Hide ' : 'Show ' }}Filters</base-button>
            <base-button v-if="!filter.all" size="sm" outline type="danger" @click="filterAll(true)">Reset</base-button>
          </div>
          <div v-show="showAdvancedOptions" class="row">
            <div class="col-12 col-md-8">
              <h3>By Level</h3>
              <div class="row">
                <div class="col-12 col-md-6">
                  <base-checkbox class="mb-2" v-model="filter.levelFilters.newbie" @input="filter.all = false">
                    <i class="ni ni-hat-3 newbie"></i> Newbie
                  </base-checkbox>
                  <base-checkbox class="mb-2" v-model="filter.levelFilters.elementary" @input="filter.all = false">
                    <i class="ni ni-hat-3 elementary"></i> Elementary
                  </base-checkbox>
                  <base-checkbox class="mb-2" v-model="filter.levelFilters['pre intermediate']" @input="filter.all = false">
                    <i class="ni ni-hat-3 preint"></i> Pre-Intermediate
                  </base-checkbox>
                  <base-checkbox class="mb-2" v-model="filter.levelFilters.intermediate" @input="filter.all = false">
                    <i class="ni ni-hat-3 intermediate"></i> Intermediate
                  </base-checkbox>
                </div>
                <div class="col-12 col-md-6">
                  <base-checkbox class="mb-2" v-model="filter.levelFilters['upper intermediate']" @input="filter.all = false">
                    <i class="ni ni-hat-3 upperint"></i> Upper Intermediate
                  </base-checkbox>
                  <base-checkbox class="mb-2" v-model="filter.levelFilters.advanced" @input="filter.all = false">
                    <i class="ni ni-hat-3 advanced"></i> Advanced
                  </base-checkbox>
                  <base-checkbox class="mb-2" v-model="filter.levelFilters.media" @input="filter.all = false">
                    <i class="ni ni-hat-3 media"></i> Media
                  </base-checkbox>
                </div>
                <div class="col-12 mb-2">
                  <router-link to="levels" class="btn btn-link px-0">About our levels</router-link>
                </div>
              </div>
            </div>
            <div class="col-12 col-md-4">
              <h3>By Status</h3>
              <base-checkbox class="mb-2" v-model="filter.statusFilters['studied']" @input="checkFilters('studied')">
                <i class="fas fa-check-circle text-danger"></i> Studied
              </base-checkbox>
              <base-checkbox class="mb-2" v-model="filter.statusFilters['not-studied']" @input="checkFilters('not-studied')">
                <i class="far fa-check-circle"></i> Not Studied
              </base-checkbox>
              <base-checkbox class="mb-2" v-model="filter.statusFilters['saved']" @input="checkFilters('saved')">
                <i class="fas fa-bookmark m-0 text-danger"></i> Saved
              </base-checkbox>
              <base-checkbox class="mb-2" v-model="filter.statusFilters['not-saved']" @input="checkFilters('not-saved')">
                <i class="far fa-bookmark m-0"></i> Not Saved
              </base-checkbox>
            </div>
          </div>
        </div>

      </div>
      <div v-show="page === 1" id="courses">
        <transition name="fade">
          <course-deck
            v-show="courseData && courseData.length > 0"
            title="Courses"
            :courseData="courseData"
            :viewMore="false"
          />
        </transition>
      </div>
      <div id="lessons">
        <transition name="fade">
          <div>
            <div v-if="lessonData && lessonData.length > 0">
              <h2 class="display-3">Lessons</h2>
              <div class="row">
                <lesson-card v-for="lesson in lessonData" :key="lesson.id" :lesson="lesson" :hide-buttons="hideButtons" class="col-6 col-md-3"></lesson-card>
              </div>
            </div>
            <div v-else-if="!searching">
              <div class="p-5 display-4">Sorry! We couldn't find any matching lessons.</div>
            </div>
          </div>
        </transition>
        <transition name="fade">
          <div id="controls" class="text-center">
            <base-pagination
              v-show="showPagination"
              :page-count="pages"
              :per-page="16"
              :value="page"
              align="center"
              @input="listener($event)"
            ></base-pagination>
          </div>
        </transition>
      </div>
      <loading :active.sync="searching"
               :can-cancel="true"
               :is-full-page="fullPage"></loading>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  import LessonCard from "../../components/Dashboard/LessonCard";
  import CourseDeck from "../../components/Dashboard/CourseDeck";

  import pickBy from "lodash.pickby";
  import isEqual from "lodash.isequal";

  // Import component
  import Loading from 'vue-loading-overlay';
  // Import stylesheet
  import 'vue-loading-overlay/dist/vue-loading.css';

  export default {
    name: "Explore",
    components: {CourseDeck, LessonCard, Loading},
    data () {
      return {
        searchTerm: '',
        lessonData: [],
        courseData: [],
        searching: false,
        enableCourseSearch: true,
        hideButtons: false,
        fullPage: true,
        perPage: 16,
        page: 1,
        pages: 1,
        showLoadMore: false,
        showPagination: false,
        showAdvancedOptions: false,
        levels: ['Newbie', 'Elementary', 'Pre Intermediate', 'Intermediate', 'Upper Intermediate', 'Advanced', 'Media'],
        filter: {
          all: true,
          levelFilters: {
            newbie: false,
            elementary: false,
            'pre intermediate': false,
            intermediate: false,
            'upper intermediate': false,
            advanced: false,
            media: false
          },
          statusFilters: {
            studied: false,
            'not-studied': false,
            saved: false,
            'not-saved': false
          }
        }
      }
    },
    computed: {
      ...mapState({
        courses: 'courses',
        latestData: 'latestData'
      })
    },
    methods: {
      async runSearch () {
        this.showAdvancedOptions = false;

        let newQuery = {};

        if (!this.filter.all) {

          let statusFilters = pickBy(this.filter.statusFilters);
          let levelFilters = pickBy(this.filter.levelFilters);

          newQuery = {query: {...{search: this.searchTerm}, ...statusFilters, ...levelFilters}};

        } else {

          newQuery = {query: {...{search: this.searchTerm}}};

        }

        if (!isEqual(this.$route.query, newQuery.query)) {

          this.$router.push({...{name: 'Explore'}, ...newQuery})

        } else {

          await this.searchLessons();

        }

      },
      ...mapActions({
        updateLatestLessons: 'updateLatestLessons'
      }),
      async listener (event) {
        if (!this.isLoading) {



          this.$router.push({ name: 'Explore', query: {...this.$route.query, ...{page: event} } });

        }
      },
      async searchLessons () {
        this.searching = true;

        this.$gtag.event('search', {
          'event_category': 'engagement',
          'event_label': this.searchTerm
        });

        //Check for filters
        let filterData = {
          levelFilters: [],
          statusFilters: []
        };

        if (!this.filter.all) {
          Object.keys(this.filter.levelFilters).forEach((level) => {
            if (this.filter.levelFilters[level]) {
              filterData.levelFilters.push(level);
            }
          });
          Object.keys(this.filter.statusFilters).forEach((status) => {
            if (this.filter.statusFilters[status]) {
              filterData.statusFilters.push(status);
            }
          });
        }

        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/search/search-all-lessons?query=' + this.searchTerm + `&skip=${(this.page - 1) * this.perPage}`, filterData)
          .then(({data}) => {
            data.courses.forEach((course) => {
              course.enrolled = this.courses.filter((item) => item.course.id === course.id).length > 0
            });

            this.courseData = data.courses;

            this.pages = Math.ceil(data.count / this.perPage);

            this.showPagination = this.pages > 1;

            // this.hideButtons = filterData.statusFilters.length <= 0;

            this.lessonData = data.lessons;

          })
          .catch(() => {});
        this.searching = false;

      },
      filterAll (status) {
        this.filter.all = status;
        this.filter.levelFilters = {
          newbie: false,
          elementary: false,
          'pre intermediate': false,
          intermediate: false,
          'upper intermediate': false,
          advanced: false,
          media: false
        };
        this.filter.statusFilters = {
          studied: false,
          'not-studied': false,
          saved: false,
          'not-saved': false
        }
      },
      checkFilters (filter) {
        this.filter.all = false;
        let currentFilter = filter;
        let oppositeFilter = '';
        if (currentFilter.split('-').length > 1) {
          oppositeFilter = currentFilter.split('-')[1]
        } else {
          oppositeFilter = `not-${currentFilter}`
        }
        this.filter.statusFilters[oppositeFilter] = false;
      }
    },
    async mounted () {

      let queryData = this.$route.query;

      if (Object.keys(queryData).length > 0) {

        Object.keys(queryData).forEach((status) => {

          if (Object.keys(this.filter.statusFilters).includes(status)) {

            this.filter.all = false;

            this.filter.statusFilters[status] = true;
          }

          if (Object.keys(this.filter.levelFilters).includes(status)) {

            this.filter.all = false;

            this.filter.levelFilters[status] = true;

          }

        })

      }

      if (this.$route.query && this.$route.query.search) {

        this.searchTerm = this.$route.query.search;

      }

      if (this.$route.query && this.$route.query.page) {

        this.page = parseInt(this.$route.query.page);
        this.pages = this.page;

      }

      await this.searchLessons();

    },
    async beforeRouteUpdate(to, from, next) {

      let queryData = to.query;

      if (queryData && queryData.search) {
        this.searchTerm = queryData.search;
      } else {
        this.searchTerm = '';
      }

      if (queryData && queryData.page) {
        this.page = parseInt(queryData.page);
      } else {
        this.page = 1;
        this.pages = 1;
      }

      await this.searchLessons();

      next()
    }
  }
</script>

