<template>
  <div>
    <div>
      <base-button v-if="premium"  class="d-block float-right" size="sm" @click="print"><i class="fas fa-print"></i> Print</base-button>
      <base-button class="float-right" size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉字' : 'Trad: 漢字'}}</base-button>
      <h2 class="h2">Grammar</h2>
    </div>
    <div class="pt-2">
      <div v-for="(point, index) in grammarSegments" :key="index">
        <card class="mt-2">
          <div class="row align-items-center">
            <div class="col-12 col-sm-auto">
              <h3 class="display-4">{{ point.grammar.name }}</h3>
            </div>
            <div class="col ml-2">
              <p v-html="point.grammar.introduction"></p>
            </div>
          </div>
          <div class="card-footer pb-0">
            <p class="text-sm mb-0">{{ point.grammar.tree }}</p>
          </div>
        </card>
        <dialogue-item
          v-for="(sentence, index) in exampleSegments(point.examples)"
          :key="index"
          :sentence-index="index"
          :cn="sentence.sentence"
          :en="sentence.en"
          :tab="`grammar-${point.grammar.id}`"
          :audioSource="audioSource(sentence, point.grammar.id)"
          class="offset-lg-2 col-lg-8" />
      </div>
    </div>
    <upgrade v-if="!premium && truncateData" hide-bullets class="col-12 m-0 p-0" title="Examples have been shortened. Upgrade to Access All Premium Features."/>
    <div v-if="premium" id="lessonGrammar" class="row d-none">
      <div class="col-3">
        <img src="@/assets/img/brand/brand.svg" class="img-fluid" alt="ChinesePod"/>
      </div>
      <h1 align="center" class="p-3">{{ lessonData.title }} - Grammar</h1>
      <div class="px-5 py-2">
        <div v-for="(point, index) in grammarSegments" :key="index" class="py-3 col-12">
          <div class="pb-3">
            <h2>{{ point.grammar.name }}</h2>
            <div>{{point.grammar.introduction}}</div>
            <small>{{point.grammar.tree}}</small>
          </div>
          <div
            v-for="(sentence, index) in exampleSegments(point.examples)"
            :key="index"
            class="pb-3">
            <div style="font-size: 1.4rem; font-family: 'Noto Sans SC', sans-serif;" v-html="sentence[charSet.slice(0,1)]"></div>
            <div v-html="convertPinyin(sentence.p)"></div>
            <div v-html="sentence.en"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import DialogueItem from "./DialogueItem";
  import Upgrade from "./Upgrade";

  import convertPinyinTones from 'pinyin-tone-converter';

  export default {
    name: "Grammar",
    components: {Upgrade, DialogueItem},
    props: {
      lessonData: Object,
      grammarData: [Object, Array]
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      audioRoot() {
        return process.env.VUE_APP_ROOT_S3 + (this.lessonData.extra ? '/extra/' : '/') +`${this.lessonData.id}/${this.lessonData.hash_code}`
      },
      grammarSegments () {
        return !this.truncateData ? this.grammarData : this.grammarData.slice(0,1)
      },
      upgradePath () {
        return this.$store.state.userInfo.upgrade.upgradePath
      },
      truncateData () {
        return !this.premium && this.upgradePath === 2
      }
    },
    mounted () {
    },
    methods: {
      async toggleCharSet () {
        await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
      },
      exampleSegments (examples) {
        return !this.truncateData ? examples : examples.slice(0,2)
      },
      audioSource (sentence, grammarId) {
        if (sentence.audio && sentence.audio.slice(0,4) === 'http') {
          return sentence.audio
        } else if (sentence.audio) {
          return process.env.VUE_APP_ROOT_S3 + '/grammar/' + `grammar_${grammarId}/` + grammarId + '/expansion/translation/mp3/' + sentence.audio
        }
      },
      print () {
        this.wantsPrint = true;
        this.$htmlToPaper('lessonGrammar');
      },
      convertPinyin (pinyin) {
        return convertPinyinTones(pinyin);
      }
    }
  }
</script>

